*, *::before, *::after{
    box-sizing: border-box;
}
body{}
img{
    max-width: 100%;
    height: auto;
    vertical-align: middle;
}
.container{
    max-width: 1320px;
    margin: 0 auto;
    padding: 0 12px;
}

/* header */
.header{
    background-color: $primary;
    img{
        max-width: 80px;
    }
}
/* footer */
.footer{
    background-color: $primary;
}